import { parse } from 'jsonc-parser';
import { getConfigStringFromEnv } from '../utils/get-config-from-env';
import { isMainConfig } from '../../schema/utils/is-main-config';
import { isDefaultApp } from '../../schema/utils/is-default-app';
import type { Config } from '../../schema/types';
import type { ClientConfig } from '../client/types';
import { MicrofrontendError } from '../../errors';
import { MicrofrontendConfigClient } from '../client';
import { type OverridesConfig, parseOverrides } from '../../overrides';
import { DefaultApplication, ChildApplication } from './application';
import { DEFAULT_LOCAL_PROXY_PORT } from './constants';
import {
  validateConfigDefaultApplication,
  validateConfigPaths,
  validateDeprecatedFields,
} from './validation';

export interface ConfigMeta {
  /**
   * The application name that the configuration is being loaded from.
   */
  fromApp: string;
}

interface ValidationOptions {
  skipValidation?: 'deprecatedFields'[];
}

/**
 * A class to manage the microfrontends configuration.
 */
export class MicrofrontendConfigIsomorphic {
  config: Config;
  isMainConfig: boolean;
  partOf?: string;
  defaultApplication?: DefaultApplication;
  childApplications: Record<string, ChildApplication> = {};
  overrides?: OverridesConfig;
  options?: Config['options'];

  private readonly serialized: {
    config: Config;
    overrides?: OverridesConfig;
    meta: ConfigMeta;
  };

  constructor({
    config,
    overrides,
    meta,
    opts,
  }: {
    config: Config;
    overrides?: OverridesConfig;
    meta: ConfigMeta;
    opts?: ValidationOptions;
  }) {
    // run validation on init
    MicrofrontendConfigIsomorphic.validate(config, opts);

    const disableOverrides =
      config.options?.disableOverrides ??
      config.options?.vercel?.disableOverrides ??
      false;
    this.overrides = overrides && !disableOverrides ? overrides : undefined;
    this.isMainConfig = isMainConfig(config);

    if (isMainConfig(config)) {
      // create applications
      for (const [appId, appConfig] of Object.entries(config.applications)) {
        const appOverrides = !disableOverrides
          ? this.overrides?.applications[appId]
          : undefined;

        if (isDefaultApp(appConfig)) {
          this.defaultApplication = new DefaultApplication(appId, {
            app: appConfig,
            overrides: appOverrides,
          });
        } else {
          this.childApplications[appId] = new ChildApplication(appId, {
            app: appConfig,
            overrides: appOverrides,
          });
        }
      }
    } else {
      this.partOf = config.partOf;
      const appOverrides = !disableOverrides
        ? this.overrides?.applications[meta.fromApp]
        : undefined;
      this.childApplications[meta.fromApp] = new ChildApplication(
        meta.fromApp,
        {
          // we don't know routing because we're not in the main config
          app: { routing: [] },
          overrides: appOverrides,
        },
      );
    }

    // the main configuration file should ALWAYS have the default
    if (isMainConfig(config) && !this.defaultApplication) {
      throw new MicrofrontendError(
        'Could not find default application in microfrontends configuration',
        {
          type: 'application',
          subtype: 'not_found',
        },
      );
    }

    this.config = config;
    this.options = config.options;
    this.serialized = {
      config,
      overrides,
      meta,
    };
  }

  static validate(config: string | Config, opts?: ValidationOptions): Config {
    const skipValidation: ValidationOptions['skipValidation'] =
      opts?.skipValidation ?? [];
    // let this throw if it's not valid JSON
    const c = typeof config === 'string' ? (parse(config) as Config) : config;

    if (isMainConfig(c)) {
      validateConfigPaths(c.applications);
      validateConfigDefaultApplication(c.applications);

      if (!skipValidation.includes('deprecatedFields')) {
        validateDeprecatedFields(c);
      }
    }

    return c;
  }

  static fromEnv({
    meta,
    cookies,
  }: {
    meta: ConfigMeta;
    cookies?: { name: string; value: string }[];
  }): MicrofrontendConfigIsomorphic {
    return new MicrofrontendConfigIsomorphic({
      config: parse(getConfigStringFromEnv()) as Config,
      overrides: parseOverrides(cookies ?? []),
      meta,
    });
  }

  isOverridesDisabled(): boolean {
    return this.options?.vercel?.disableOverrides ?? false;
  }

  getConfig(): Config {
    return this.config;
  }

  getApplicationsByType(): {
    defaultApplication?: DefaultApplication;
    applications: ChildApplication[];
  } {
    return {
      defaultApplication: this.defaultApplication,
      applications: Object.values(this.childApplications),
    };
  }

  getChildApplications(): ChildApplication[] {
    return Object.values(this.childApplications);
  }

  getAllApplications(): (DefaultApplication | ChildApplication)[] {
    return [
      this.defaultApplication,
      ...Object.values(this.childApplications),
    ].filter(Boolean) as (DefaultApplication | ChildApplication)[];
  }

  getApplication(name: string): DefaultApplication | ChildApplication {
    // check the default
    if (this.defaultApplication?.name === name) {
      return this.defaultApplication;
    }
    const app = this.childApplications[name];
    if (!app) {
      throw new MicrofrontendError(
        `Could not find microfrontends configuration for application "${name}"`,
        {
          type: 'application',
          subtype: 'not_found',
        },
      );
    }

    return app;
  }

  getApplicationByProjectId(
    projectId: string,
  ): DefaultApplication | ChildApplication | undefined {
    // check the default
    if (this.defaultApplication?.projectId === projectId) {
      return this.defaultApplication;
    }

    return Object.values(this.childApplications).find(
      (app) => app.projectId === projectId,
    );
  }

  /**
   * Returns the default application. This can throw if the default application
   * is undefined ( )
   */
  getDefaultApplication(): DefaultApplication {
    if (!this.defaultApplication) {
      throw new MicrofrontendError(
        'Could not find default application in microfrontends configuration',
        {
          type: 'application',
          subtype: 'not_found',
        },
      );
    }

    return this.defaultApplication;
  }

  /**
   * Returns the configured port for the local proxy
   */
  getLocalProxyPort(): number {
    return (
      this.config.options?.localProxyPort ??
      this.config.options?.localProxy?.port ??
      DEFAULT_LOCAL_PROXY_PORT
    );
  }

  /**
   * Serializes the class back to the Schema type.
   *
   * NOTE: This is used when writing the config to disk and must always match the input Schema
   */
  toSchemaJson(): Config {
    return this.serialized.config;
  }

  toClientConfig(): MicrofrontendConfigClient {
    const applications: ClientConfig['applications'] = Object.fromEntries(
      Object.entries(this.childApplications).map(([name, application]) => [
        name,
        {
          default: false,
          routing: application.routing,
        },
      ]),
    );

    // add the default application
    if (this.defaultApplication) {
      applications[this.defaultApplication.name] = {
        default: true,
      };
    }

    return new MicrofrontendConfigClient({
      applications,
    });
  }

  serialize(): {
    config: Config;
    overrides?: OverridesConfig;
    meta: ConfigMeta;
  } {
    return this.serialized;
  }
}
