import { parse } from 'jsonc-parser';
import { Ajv, type ErrorObject } from 'ajv';
import type { Config } from '../../schema/types';
import { MicrofrontendError } from '../../errors';
import { SCHEMA } from '../../schema/utils/load';

// NOTE(mknichel): AJV returns very imprecise validation errors that include
// multiple conflicting statements. We should improve this in the future.
function filterAjvErrors(
  errors: ErrorObject[] | null | undefined,
): ErrorObject[] {
  if (!errors) {
    return [];
  }
  return errors.filter((error) => {
    return (
      error.keyword === 'additionalProperties' || error.keyword === 'required'
    );
  });
}

export function validateSchema(configString: string): Config {
  const parsedConfig = parse(configString) as Config;
  const ajv = new Ajv();
  const validate = ajv.compile(SCHEMA);
  const isValid = validate(parsedConfig);
  if (!isValid) {
    throw new MicrofrontendError(
      `Invalid microfrontends config:\n - ${ajv.errorsText(filterAjvErrors(validate.errors), { separator: '\n - ' })}\n\nSee https://openapi.vercel.sh/microfrontends.json for the schema.`,
      { type: 'config', subtype: 'does_not_match_schema' },
    );
  }
  return parsedConfig;
}
