import {
  type ConfigMeta,
  MicrofrontendConfigIsomorphic,
} from '../../microfrontends-config/isomorphic';
import { MicrofrontendChildConfig } from '../../microfrontends-config/isomorphic/child';
import { MicrofrontendMainConfig } from '../../microfrontends-config/isomorphic/main';
import type { MicrofrontendsConfig } from '../types';
import type { Config } from '../../schema/types';
import type { OverridesConfig } from '../../overrides';
import { isMainConfig } from '../../schema/utils/is-main-config';

export class Microfrontends {
  config: MicrofrontendsConfig;

  constructor({
    config,
    overrides,
    meta,
  }: {
    config: Config;
    overrides?: OverridesConfig;
    meta: ConfigMeta;
  }) {
    if (isMainConfig(config)) {
      this.config = new MicrofrontendMainConfig({ config, overrides, meta });
    } else {
      this.config = new MicrofrontendChildConfig({ config, overrides, meta });
    }
  }

  isChildConfig(): boolean {
    return this.config instanceof MicrofrontendChildConfig;
  }

  static fromEnv({
    cookies,
    meta,
  }: {
    cookies?: { name: string; value: string }[];
    meta: ConfigMeta;
  }): Microfrontends {
    const config = MicrofrontendConfigIsomorphic.fromEnv({
      cookies,
      meta,
    });
    return new Microfrontends(config.serialize());
  }
}
