import type {
  Application as ApplicationConfig,
  DefaultApplication as DefaultApplicationConfig,
  ChildApplication as ChildApplicationConfig,
  PathGroup,
} from '../../schema/types';
import type { ApplicationOverrideConfig } from '../../overrides';
import { validateAppPaths } from './validation';
import { generateAssetPrefixFromName } from './utils/generate-asset-prefix';
import { Host, LocalHost } from './host';

export class Application {
  readonly default: boolean;
  name: string;
  development: {
    local: LocalHost;
    fallback?: Host;
  };
  fallback?: Host;
  projectId?: string;
  overrides?: {
    environment?: Host;
  };
  readonly serialized: ApplicationConfig;

  constructor(
    name: string,
    {
      app,
      overrides,
      isDefault,
    }: {
      app: ApplicationConfig;
      overrides?: ApplicationOverrideConfig;
      isDefault?: boolean;
    },
  ) {
    this.name = name;
    this.development = {
      local: new LocalHost({
        appName: name,
        localPort: app.development?.localPort,
        ...app.development?.local,
      }),
      fallback: app.development?.fallback
        ? new Host(app.development.fallback)
        : undefined,
    };
    if (app.development?.fallback) {
      this.fallback = new Host(app.development.fallback);
    } else if (app.production) {
      this.fallback = new Host(app.production);
    }
    this.projectId = app.projectId ?? app.vercel?.projectId;
    this.overrides = overrides?.environment
      ? {
          environment: new Host(overrides.environment),
        }
      : undefined;
    this.default = isDefault ?? false;
    this.serialized = app;
  }

  isDefault(): boolean {
    return this.default;
  }

  getAssetPrefix(): string {
    return generateAssetPrefixFromName({ name: this.name });
  }

  serialize(): ApplicationConfig {
    return this.serialized;
  }
}

export class DefaultApplication extends Application {
  readonly default = true;
  // TODO: remove this once the config simplification migration is complete.
  /** @deprecated use fallback instead. */
  production?: Host;
  fallback: Host;

  constructor(
    name: string,
    {
      app,
      overrides,
    }: {
      app: DefaultApplicationConfig;
      overrides?: ApplicationOverrideConfig;
    },
  ) {
    super(name, {
      app,
      overrides,
      isDefault: true,
    });

    const fallbackHost = app.development?.fallback ?? app.production;
    if (fallbackHost === undefined) {
      // TODO: remove this one app.developement.fallback is required after the migration.
      throw new Error(
        '`app.production` or `app.development.fallback` must be set in the default application in microfrontends.json.',
      );
    }
    this.fallback = new Host(fallbackHost);
    if (app.production) {
      this.production = new Host(app.production);
    }
  }

  getAssetPrefix(): string {
    return '';
  }
}

export class ChildApplication extends Application {
  readonly default = false;
  routing: PathGroup[];

  constructor(
    name: string,
    {
      app,
      overrides,
    }: {
      app: ChildApplicationConfig;
      overrides?: ApplicationOverrideConfig;
    },
  ) {
    // validate
    ChildApplication.validate(name, app);

    super(name, {
      app,
      overrides,
      isDefault: false,
    });

    this.routing = app.routing;
  }

  static validate(name: string, app: ChildApplicationConfig): void {
    // validate routes
    validateAppPaths(name, app);
  }
}
