import type { ChildConfig } from '../../schema/types';
import type { OverridesConfig } from '../../overrides';
import { type ConfigMeta, MicrofrontendConfigIsomorphic } from '.';

export class MicrofrontendChildConfig extends MicrofrontendConfigIsomorphic {
  isMainConfig = false;
  partOf?: string;

  constructor({
    config,
    overrides,
    meta,
  }: {
    config: ChildConfig;
    overrides?: OverridesConfig;
    meta: ConfigMeta;
  }) {
    super({ config, overrides, meta });
    this.partOf = config.partOf;
  }
}
