import type {
  HostConfig as RemoteHostConfigSchema,
  LocalHostConfig as LocalHostConfigSchema,
} from '../../schema/types';
import { generatePortFromName } from './utils/generate-port';

interface HostOptions {
  isLocal?: boolean;
}

export class Host {
  protocol: 'http' | 'https';
  host: string;
  port?: number;
  local: boolean | undefined;

  constructor(
    hostConfig: RemoteHostConfigSchema | string,
    options?: HostOptions,
  ) {
    if (typeof hostConfig === 'string') {
      ({
        protocol: this.protocol,
        host: this.host,
        port: this.port,
      } = Host.parseUrl(hostConfig));
    } else {
      const { protocol = 'https', host, port } = hostConfig;
      this.protocol = protocol;
      this.host = host;
      this.port = port;
    }
    this.local = options?.isLocal;
  }

  private static parseUrl(url: string): {
    protocol: Host['protocol'];
    host: string;
    port?: number;
  } {
    let hostToParse = url;
    if (!/^https?:\/\//.exec(hostToParse)) {
      hostToParse = `https://${hostToParse}`;
    }
    const parsed = new URL(hostToParse);
    if (!parsed.hostname) {
      throw new Error(Host.getMicrofrontendsError(url, 'requires a host'));
    }
    if (parsed.hash) {
      throw new Error(
        Host.getMicrofrontendsError(url, 'cannot have a fragment'),
      );
    }
    if (parsed.username || parsed.password) {
      throw new Error(
        Host.getMicrofrontendsError(
          url,
          'cannot have authentication credentials (username and/or password)',
        ),
      );
    }
    if (parsed.pathname !== '/') {
      throw new Error(Host.getMicrofrontendsError(url, 'cannot have a path'));
    }
    if (parsed.search) {
      throw new Error(
        Host.getMicrofrontendsError(url, 'cannot have query parameters'),
      );
    }
    const protocol = parsed.protocol.slice(0, -1) as Host['protocol'];
    return {
      protocol,
      host: parsed.hostname,
      port: parsed.port ? Number.parseInt(parsed.port) : undefined,
    };
  }

  private static getMicrofrontendsError(url: string, message: string): string {
    return `Microfrontends configuration error: the URL ${url} in your microfrontends.json ${message}.`;
  }

  isLocal(): boolean {
    return this.local || this.host === 'localhost' || this.host === '127.0.0.1';
  }

  toString(): string {
    const url = this.toUrl();
    // strip the trailing slash
    return url.toString().replace(/\/$/, '');
  }

  toUrl(): URL {
    const url = `${this.protocol}://${this.host}${this.port ? `:${this.port}` : ''}`;
    return new URL(url);
  }
}

/**
 * A Host subclass with defaults for locally running applications
 */
export class LocalHost extends Host {
  constructor({
    appName,
    localPort,
    ...hostConfig
  }: LocalHostConfigSchema & { appName: string; localPort?: number }) {
    // set defaults for local
    const host = hostConfig.host ?? 'localhost';
    const port =
      localPort ?? hostConfig.port ?? generatePortFromName({ name: appName });
    const protocol = hostConfig.protocol ?? 'http';

    super({ protocol, host, port });
  }
}
